import React from 'react';
import { Typography, Button, Grid, Paper } from '@material-ui/core';
import DatePicker from 'react-datepicker';

import './summary.css';

class SummaryComponent extends React.Component {
    constructor(props) {
        super(props);
        //STATE:
        //summary:
        //checkBookId
        //total
        //symbol
        //value
        //paymentTotal
        //symbol
        //value
        //depositTotal
        //symbol
        //value
    }

    getSafe(fn, defaultValue) {
        try {
            return fn();
        } catch (e) {
            return defaultValue;
        }
    }

    render() {
        return (
            <div className="summary">
                <Grid container
                    spacing={0}
                    justify="flex-end">
                    <Grid item xs={3}>
                        <Paper className={this.props.classes.filterPaper}>
                            <Typography style={{ color: 'rgba(255,255,255, 0.7)', marginRight: 15}}>Filter Future Date:</Typography>
                            
                            <DatePicker className={this.props.classes.summaryDatePicker} selected={this.getSafe(() => this.props.summary.baseDate, null)} onChange={this.props.handleBaseDate} />
                        </Paper>
                    </Grid>
                    <Grid item xs={2}>
                        <Paper className={this.props.classes.filterPaper}>
                            <Button className={this.props.classes.filterButton} onClick={this.props.handleBaseDateClear}>Clear</Button>
                        </Paper>
                    </Grid>
                </Grid>


                <Typography variant="h3">{'$' + this.getSafe(() => this.props.summary.total.value, "---")}</Typography>
                <Typography variant="h4">{'Payments: -$' + this.getSafe(() => this.props.summary.paymentTotal.value, "---")}</Typography>
                <Typography variant="h4">{'Deposits: +$' + this.getSafe(() => this.props.summary.depositTotal.value, "---")}</Typography>

                <Typography>{"Future Total Change: $" + this.getSafe(() => this.props.summary.futureTotal.value, "---")}</Typography>
                <Typography>{"Future Payment Change: -$" + this.getSafe(() => this.props.summary.futurePaymentTotal.value, "---")}</Typography>
                <Typography>{"Future Deposit Change: +$" + this.getSafe(() => this.props.summary.futureDepositTotal.value, "---")}</Typography>

                {/* <Typography>MVP: Reports, Calendar+Plug Reports, Input Validation, Database, Filter, Filter Summary, Accounts</Typography>
                <Typography>Monday: Chemistry IA</Typography>
                <Typography>Tuesday: Reports, Calendar+Plug Reports, Input Validation, Database</Typography>
                <Typography>Wednesday: Database, Filter</Typography>
                <Typography>Thursday: Filter Summary, Accounts, Quality of Life</Typography> */}
            </div>
        );
    }
}

export default SummaryComponent